import axios from 'axios'
import { Toast } from 'vant'
const url = 'https://sfrz.wsbs.shxga.gov.cn/sxfama/commCheckCertCode'

const handleDetection = (imgUrl, certToken) => {
  const matchString = 'base64,'
  const index = imgUrl.indexOf(matchString)
  if (index === -1) return '匹配不到图片地址'
  const img = imgUrl.substr(index + matchString.length)
  return axios.post(url, {
    certToken,
    portrait: img
  }).then((res) => {
    console.log(res)
    if (res.isAlive) Toast.success('认证成功')
  })
}

export default handleDetection